.default__btn {
  overflow: hidden;
  position: relative;
  width: fit-content;
  padding: 16px 60px;
  border-radius: 20px;
  background: #ffd842;
  transition: all 0.4s ease;
  text-align: center;
  &:hover {
    &::before {
      width: 100%;
      left: 0;
      right: auto;
      border-radius: 20px;
    }
  }
  &:hover span {
    color: #151515;
    transition: 0.2s;
  }
  &::before {
    content: " ";
    width: 0;
    transition: 0.5s ease;
    border-radius: 20px;
    height: 100%;
    background: linear-gradient(90deg, #5adbee 0%, rgba(90, 219, 238, 0) 100%);
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    border-radius: 20px;
  }
  span {
    color: #151515;
    font-size: 16px;
    font-weight: 700;
    position: relative;
    z-index: 3;
    transition: 0.2s;
    border-radius: 20px;
  }
}
