@import "../../scss/mixin";

.root {
  &, *{
    font-size: 10rem;
    color:#000;
    font-weight: 500;
    line-height: 1.3;
    @media (max-width:580px) {
      @include adaptive-font(180, 49)
    }
  }
  font-size: 10rem;
  line-height: 11rem;
  color:#000;
  font-weight: 500;
  @media (max-width: 800px) {
    font-size: 6rem;
  }
  @media (max-width: 500px) {
    font-size: 3.5rem;
  }
}