@import '../../scss/mixin.scss';
section {
  &.home_banner {
    background: linear-gradient(238.74deg, #FFEDAB 40.91%, #FFC700 99.21%);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden !important;
    padding: 7rem 0;
    @media(max-width:992px) {
      padding: 12rem 0 7rem;
    }
    .circleWhiteUp {
      position: absolute;
      @include adapt('width', 97, 25, 0);
      @include adapt('height', 102, 25, 0);
      @include adapt('top', -48, -12, 0);
      @include adapt('left', 72, 21, 0);
      background: #FFFFFF;
      border-radius: 50%;
    }
    .circleYellowUp {
      position: absolute;
      @include adapt('width', 97, 25, 0);
      @include adapt('height', 102, 25, 0);
      @include adapt('top', -54, -14, 0);
      @include adapt('left', 76, 23, 0);
      background: #FFDB5B;
      border-radius: 50%;
    }
    .upDecoration {
      position: absolute;
      @include adapt('top', 11, 5, 0);
      @include adapt('width', 100, 30, 0);
      @include adapt('height', 49.7, 13, 0);
      z-index: 1;
      animation: upDecoration 2s ease forwards;
      @keyframes upDecoration {
        0% {
          right: -48rem;
          transform: rotate(90deg);
        }
        100% {
          @include adapt('right', -20, -10, 0);
          transform: rotate(0deg);
        }
      }
    }
    .upDecoration2 {
      position: absolute;
      @include adapt('top', 8, 2, 0);
      @include adapt('width', 35, 9, 0);
      @include adapt('height', 29, 8, 0);
      z-index: 1;
      animation: upDecoration2 3s ease forwards;
      @keyframes upDecoration2 {
        0% {
          @include adapt('right', -20, -5, 0);
        }
        100% {
          right: 0;
        }
      }
    }
    .container {
      max-width: 90rem;
      position: relative;
      z-index: 3;
    }
    h1 {
      position: relative;
      z-index: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
      span{
        font-size: 14.7rem;
        text-align: center;
        font-weight: 700;
        display: flex;
        align-items: center;
        justify-content: center;
        @media (max-width:1000px) {
          @include adaptive-font(140, 50);
        }
      }
    }
    p {
      font-size: 2rem;
      text-align: center;
      padding: 6rem 10rem;
      @media (max-width: 700px) {
        padding: 6rem 0;
      }
    }
    .btn {
      display: block;
      margin: 0 auto;
      background: #fff;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      @media(max-width:340px) {
        padding: 16px 40px;
      }
    }
  }
  &.introDecor {
    position: relative;
    z-index: 2;
    width: 100%;
    height: fit-content;
    /* overflow: hidden; */
    .leftW {
      position: absolute;
      z-index: 1;
      left: 0;
      @include adapt('bottom', -28, -7.5, 0);
      @include adapt('width', 19, 6, 0);
      @include adapt('height', 60, 20, 0);
      animation: leftW 2s ease forwards;
      @keyframes leftW {
        0%{
          height: 0;
          width: 0;
        }
        100%{
          @include adapt('height', 60, 20, 0);
        }
      }
    }
    .rightW {
      position: absolute;
      @include adapt('bottom', -16, -1.5, 0);
      @include adapt('left', 3, -4, 0);
      @include adapt('width', 61, 28, 0);
      @include adapt('height', 79, 22, 0);
      z-index: 1;
      animation: rightW 2s ease forwards;
      @keyframes rightW {
        0%{
          height: 0;
        }
        100%{
          @include adapt('height', 79, 22, 0);
        }
      }
    }
    .circle {
      position: absolute;
      z-index: 2;
      @include adapt('top', -13, -4, 0);
      @include adapt('width', 22, 10, 0);
      @include adapt('height', 22, 10, 0);
      animation: circle 2s ease forwards;
      @include adapt('right', 7, 3, 0);
      @keyframes circle {
        0% {
          transform: scale(0);
        }
        70%{
          transform: scale(1.1);
        }
        100% {
          transform: scale(1);
        }
      }
    }
    .circleYellowDown {
      position: absolute;
      @include adapt('width', 80, 20, 0);
      @include adapt('height', 80, 20, 0);
      @include adapt('left', -22, -5, 0);
      @include adapt('bottom', -26, -6, 0);
      background: #FFDB5B;
      border-radius: 50%;
      z-index: 1;
    }
    .circleWhiteDown {
      position: absolute;
      background: #FFFFFF;
      border-radius: 50%;
      @include adapt('width', 97, 25, 0);
      @include adapt('height', 102, 25, 0);
      @include adapt('left', -24, -6, 0);
      @include adapt('bottom', -30, -8, 0);
      z-index: 0;
    }
  }
  &.services__we__provide {
    position: relative;
    z-index: 10;
    padding-top: 5rem;
    .container {
      @media (max-width: 1350px) {
        padding: 0 4vw;
      }
    }
    .title__wrapper {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 6rem;
      span {
        &:nth-child(2) {
          margin-left: 5rem;
        }
      }
    }
    .acquired__technologies {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      @media (max-width: 786px) {
        flex-direction: column;
        align-items: center;
      }
      &__col {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        &:not(:last-child) {
          padding-right: 3%;
        }
        @media (max-width: 786px) {
          &:not(:last-child) {
            margin-bottom: 4rem;
          }
          padding: 0 !important;
        }
        .top {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 2rem;
          position: relative;
          width: 100%;
          .tech__name {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            span {
              @include fontChar(#151515, 24px, 400);
            }
          }
          .decor {
            width: 6rem;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
            border-bottom-left-radius: 30px;
            @media (max-width: 786px) {
              width: 30%;
            }
          }
        }
        .subtitle {
          @include fontChar(#151515, 17px, 400);
        }
      }
    }
  }
  &.what__is__webincore {
    padding-top: 5rem;
    min-height: 103rem;
    .container {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      @media(min-width: 1340px) {
        padding: 0 1.3vw 0 4vw;
      }
      @media(max-width: 1340px) {
        padding: 0 4vw 0 7vw;
      }
      @media (max-width: 1200px) {
        flex-direction: column-reverse;
        align-items: center;
      }
      @media (max-width: 1200px) {
        padding: 0 4vw;
      }
    }
    .left {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      @media(max-width: 1200px) {
        margin: 5rem 0;
      }
    }
    .logo__wrapper {
      @media(max-width: 390px) {
        width: 100%;
      }
      .logo {
        &:nth-child(2) {
          transform: rotate(50deg) translate(13px, 69px);
          @media(max-width: 390px) {
            transform: rotate(50deg) translate(13px, 69px) scale(.9);
          }
        }
        @media(max-width: 390px) {
          transform: scale(.9);
          width: 50%;
        }
      }
    }
    .achievements {
      position: relative;
      margin-top: 10rem;
      width: 100%;
      @media(max-width: 1200px) {
        margin-top: 7rem;
      }
      @media(max-width: 640px) {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }
      @media(max-width: 390px) {
        margin-top: 2rem;
      }
      @media (max-width: 430px) {
        transform: scale(.9);
      }
      &__col__round {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        border-radius: 50%;
        text-align: center;
        position: absolute;
        @media(max-width: 640px) {
          position: static;
          &:not(:last-child) {
            margin-bottom: 2rem;
          }
        }
        &:nth-child(1) {
          top: 0;
          left: -5%;
          width: 25rem;
          height: 25rem;
          z-index: 3;
          background: linear-gradient(180deg, #B0F5FF 0%, rgba(176, 245, 255, 0) 100%);
          @media(max-width: 1200px) {
            left: -12%;
          }
          @media(max-width: 640px) {
            left: 6%;
            top: 23%;
            width: 17rem;
            height: 17rem;
          }
        }
        &:nth-child(2) {
          left: 57%;
          top: 8rem;
          width: 25rem;
          height: 25rem;
          z-index: 5;
          @media(max-width: 1200px) {
            left: 43%;
          }
          background: linear-gradient(180deg, #B0F5FF 0%, rgba(176, 245, 255, 0) 100%);
          @media(max-width: 640px) {
            left: 42%;
            width: 20rem;
            height: 20rem;
          }
        }
        &:nth-child(3) {
          width: 30rem;
          left: -16%;
          top: 22rem;
          height: 30rem;
          z-index: 4;
          @media(max-width: 1200px) {
            left: -38%;
          }
          background: linear-gradient(180deg, #5ADBEE 0%, rgba(90, 219, 238, 0) 100%);
          @media(max-width: 640px) {
            left: -10%;
            top: 15rem;
            width: 20rem;
            height: 20rem;
          }
        }
        &:nth-child(4) {
          top: 29rem;
          left: 56%;
          width: 40rem;
          height: 40rem;
          background: linear-gradient(180deg, #FFDC62 0%, rgba(255, 242, 196, 0) 100%);
          @media(max-width: 1300px) {
            width: 35rem;
            height: 35rem;
          }
          @media(max-width: 1200px) {
            left: 34%;
          }
          @media(max-width: 640px) {
            left: 38%;
            top: 22rem;
            width: 23rem;
            height: 23rem;
          }
        }
        .count {
          @include fontChar(#151515, 60px, 400);
          margin-bottom: .5rem;
          @media (max-width: 640px) {
            font-size: 36px;
          }
          &__describing {
            @include fontChar(#151515, 16px, 400);
            width: 100%;
            max-width: 16rem;
            @media (max-width: 640px) {
              font-size: 12px;
            }
          }
        }
      }
    }
    .right {
      width: 50%;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      @media (max-width: 1200px) {
        width: 100%;
        align-items: center;
      }
      h2 {
        line-height: 1.1 !important;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin-bottom: 6rem;
        @media (max-width: 1200px) {
          align-items: center;
        }
        span {
          &:nth-child(2) {
            margin-right: 5rem;
            @media (max-width: 1200px) {
              margin: 0;
            }
          }
        }
      }
      .about__subtitle {
        margin-bottom: 3rem;
        p {
          @include fontChar(#151515, 18px, 400);
          @media (max-width: 1200px) {
            text-align: center;
          }
          text-align: left;
          &:not(:last-child) {
            margin-bottom: 2.5rem;
          }
        }
      }
    }
  }
  &.our_team {
    padding-bottom: 14rem;
    @media (max-width: 1200px) {
      margin-top: 55rem;
    }
    @media (max-width: 640px) {
      margin-top: 0rem;
    }
    .title {
      text-align: right;
      padding-bottom: 6rem;
    }
    .line {
      display: flex;
      justify-content: space-between;
      @media (max-width: 1116px) {
        flex-wrap: wrap;
      }
      .left {
        width: 100%;
        @include fb(fs, sb);
        @media (max-width: 850px) {
          width: 100%;
          justify-content: center;
          flex-wrap: wrap;
        }
      }
      .right {
        width: 100%;
        max-width: 40.7rem;
        @media (max-width: 1116px) {
          max-width: 100%;
        }
      }
    }
    .block {
      width: 100%;
      max-width: 42rem;
      overflow: hidden;
      margin-right: 1rem;
      @media (max-width: 1115px) {
        &:last-child {
          margin-right: 0rem;
        }
        &:first-child {
          margin-right: 1rem;
        }
      }
      @media (max-width: 850px) {
        &:first-child {
          margin-right: 0rem;
        }
      }
      @media (max-width: 1115px) {
        max-width: 70rem;
      }
      .img {
        max-width: 42.8rem;
        height: 59.7rem;
        @media (max-width: 1115px) {
          max-width: 70rem;
          height: 77.7rem;
        }
        @media (max-width: 850px) {
          height: 80vh;
          img {
            object-position: center top;
          }
        }
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      &_name {
        @include fb(c, sb);
        padding: 2rem 0.5rem;
        p {
          @include font(1.8, 2.8, 700);
        }
        .dots {
          cursor: pointer;
          position: relative;
          transition: 1s;
        }
        .dots_active {
          transition: 1s;
          transform: rotate(90deg);
        }
      }
      &_speciality {
        padding: 0 .5rem;
        @include font(1.4, 2.4, 500);
        color: #8B949D;
      }
      &_info {
        height: 0;
        animation: displayNone 1s ease;
        &_active {
          animation: displayBlock 1s ease forwards;
        }
        p {
          @include font(1.25, 2.4, 500);
          padding: 0 0.5rem;
          margin-top: .5rem;
        }
      }
    }
    .group_photo {
      @include fb(fs, sb, w);
      position: relative;
      grid-gap: 3px;
      @media (max-width: 1116px) {
        @include adapt('grid-gap',1.8,.3,0);
        
      }
      .img {
        width: calc(32%);
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
    .btn {
      display: block;
      margin-left: auto;
      position: absolute;
      right: 0;
      bottom: -12rem;
      @media (max-width: 1115px) {
        width: 100%;
      }
    }
    @keyframes displayBlock {
      0% {
        height: 0;
      }
      100% {
        height: 14rem;
      }
    }
    @keyframes displayNone {
      0% {
        height: 14rem;
      }
      100% {
        height: 0;
      }
    }
  }
  &.clientTrust {
    .title {
      text-align: center;
      max-width: 72.6rem;
      margin: 0 auto;
      padding: 0 2rem;
    }
    .subtitle {
      max-width: 87.6rem;
      text-align: center;
      margin: 0 auto;
      @include font(1.6, 2.6);
      padding: 3rem 2rem;
    }
    .container {
      @include fb(fs, sb);
      @media (max-width: 1130px) {
        @include fb(c, c, w);
        grid-gap: 5rem;
      }
    }
    .card {
      max-width: 36rem;
      width: 100%;
      @include fb(fs, c);
      flex-direction: column;
      position: relative;
      .front {
        transform: rotateY(0deg);
        z-index: 1;
        position: relative;
        img {
          z-index: 1;
          width: 100%;
          height: 100%;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
      }
      .back {
        position: absolute;
        z-index: 3;
        transform: rotateY(-90deg);
        transition: all 1s ease;
        background: #ECEEF0;
        top: 0;
        left: 0;
        height: 100%;
      }
      &:hover {
        .front {
          transform: rotateY(-90deg);
          z-index: 1;
          transition: .4s ease;
        }
        .back {
          transform: rotateY(0);
          z-index: 1;
        }
      }
      .content {
        padding: 5rem 4rem;
        * {
          margin-bottom: 1.5rem;
        }
      }
      &_title {
        @include font(1.8, 2.6, 700);
        @media (max-width: 500px) {
          margin-top: -2rem;
        }
      }
      &_subtitle {
        @include font(1.4, 2.4, 500);
        color: #8B949D;
      }
      &_description {
        @include font(1.4, 2.4, 500);
        margin: 2rem 0;
        height: 10rem;
        display: -webkit-box;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: normal;
      }
    }
    .btn {
      position: absolute;
    }
  }
}


