.link__btn{
    color: #151515;
    transition: all .3s ease;
    position: relative;
    &::before{
        content: '';
        position: absolute;
        top: -7px;
        bottom: -7px;
        right: -7px;
        left: -7px;
    }
    svg{
        fill: #151515;
        transition: all .3s ease;
    }
    &:hover{
        color: rgb(66, 142, 255);
    }
}

.footer{
    width: 100%;
    position: relative;
    overflow: hidden;
    background: #eceef0;
    padding: 4rem 0;
    margin-top: 5rem;
    @media (max-width:992px) {
        padding: 6rem 0;
    }

    .container{
        padding: 0 6.5vw;
        *{
            font-size: 16px;
            font-weight: 700;
        }
    }

    &__socials{
        width: 100%;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        @media (max-width:992px) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }
    &__col{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        justify-content: flex-start;
        max-width: 23.8rem;
        @media (max-width:992px) {
            align-items: center;
            &:not(last-child) {
                margin-bottom: 4rem;
            }
        }
        &:nth-child(2) {
            align-items: center;
            /* max-width: 21rem; */
        }
        
        & > p{
            margin-bottom: 2rem;
            color: #8b949d;
        }
        nav{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            @media (max-width:992px) {
                align-items: center;
            }
            .link__btn{
                &:not(:last-child) {
                    margin-bottom: 2rem;
                }
            }
        }
        &__link__to__socials{
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            flex-wrap: wrap;
            
            a{
                height: 3.2rem;
                background: #151515;
                border-radius: 10px;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 7px;
                transition: all .4s ease;
                flex: 0 0 3.4rem;
                svg{
                    fill: #fff;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
                &:hover{
                    background: rgb(66, 142, 255);
                }
            }
        }
        &__contacts {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            @media (max-width:992px) {
                align-items: center;
            }
            .link__btn{
                display: flex;
                align-items: center;
                justify-content: center;
                &:first-child{
                    margin-bottom: 3rem;
                    @media (max-width:992px) {
                        margin-bottom: 1.2rem;
                    }
                }
                span{
                    font-size: 19px !important;
                    font-weight: 700;
                    margin-left: 1rem;
                    transition: all .3s ease;
                }
                svg{
                    font-size: 23px !important;
                }
                &:hover {
                    & span{
                        color:rgb(66, 142, 255);
                    }
                    & svg{
                        fill:rgb(66, 142, 255);
                    }
                }
            }
        }
    }

    &__info{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin-top: 2rem;
        a{
            span{
                font-size: 2rem !important;
            }
            margin-bottom: 1.5rem;
        }
        p{
            font-size: 13px !important;
        }
    }

}


