.root {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  background: transparent;
  z-index: 25;
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10rem;
  }

  .logo {
    z-index: 3;

    a {
      font-size: 2rem;
      color: #fff;
      font-weight: 700;
    }
    .activeLogo{
      clipPath{
        rect{
          fill: white;
        }
      }
    }
  }

  .menu_list {
    position: absolute;
    left: 0;
    top: -90rem;
    padding: 8rem 0;
    min-height: 100vh;
    width: 100%;
    justify-content: center;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: 1.5s;
    &::before{
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1;
      background: linear-gradient(-45deg, #ee7752, #d27373, #23a6d5, #23d5ab);
      background-size: 400% 400%;
      animation: gradient 8s ease infinite;
    }
    ul {
      width: 100%;
    }

    li {
      text-align: center;
      padding: 1.5rem 0;
      width: 100%;

      a {
        font-size: 4.6rem;
        color: #fff;
        font-weight: 700;
        cursor: pointer;
        width: fit-content;
      }
    }
  }


  .menu_list_active {
    top: 0;
  }

  button {
    display: flex;
    align-items: center;
    cursor: pointer;
    width: 8rem;
    position: relative;
    z-index: 3;
    border: none;

    p {
      font-size: 2rem;
      color: #000;
      font-weight: 700;
      padding-right: .7rem;
      transition: .4s;
    }
    .test{
      color:white;
      transition: .4s;
    }

    .btn {
      width: 1.3rem;
      position: relative;

      span {
        display: block;
        height: 0.2rem;
        width: 100%;
        background: #000;
        margin: 0.3rem auto;
        position: relative;
        transition: .4s;
      }
    }
  }

  .activeBtn {
    transform: rotate(-45deg);
    height: 2rem;
    position: fixed;
    transition: .4s;
    z-index: 10;
    span {
      background: #fff !important;
      margin: 0;

      &:nth-child(1) {
        top: 5px;
      }

      &:nth-child(2) {
        transform: rotate(90deg);
        height: 2.2px;
      }

      &:nth-child(3) {
        width: 0;
      }
    }
  }
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}